.previewContainer {
  h1, h2, h3, h4, h5, h6 {
    font-weight: bold;
  }

  h1 {
    font-size: 2.5rem;
    margin: 1.5rem 0;
  }

  h2 {
    font-size: 2rem;
    margin: 1.25rem 0;
  }

  h3 {
    font-size: 1.75rem;
    margin: 1.125rem 0;
  }

  h4 {
    font-size: 1.5rem;
    margin: 1rem 0;
  }

  h5 {
    font-size: 1.25rem;
    margin: 0.875rem 0;
  }

  h6 {
    font-size: 1rem;
    margin: 0.75rem 0;
  }

  p {
    font-size: 1rem;
    margin: 0.75rem 0;
  }

  a {
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
  }

  a:hover {
    color: #0056b3;
    text-decoration: underline;
  }

  ul {
    list-style: inside;
    padding-left: 1.5rem;
  }

  ol {
    list-style: decimal;
    padding-left: 1.5rem;
  }

  code {
    display: block;
    width: 100%;
    background: #f8f8f8;
    border: 1px solid #e8e8e8;
    border-radius: 3px;
    color: #333;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 0.9rem;
    padding: 0.2rem 0.4rem;
  }
}
